.orders{

}
.header{
    margin-top: 3.8rem;
    margin-bottom: 5.7rem;
    color: #000;
    font-size: 2.8rem;
    font-family: Poppins, sans-serif;
    font-weight: 400;
    text-transform: uppercase;
}
.order{
    margin-left: 9.6rem;
    padding: 3rem 2rem 3rem 1rem;
    width: 93.8rem;
    display: flex;
}
.itemImage{
    margin-right: 2rem;
    width: 18rem;
    height: 20rem;
    background: #D9D9D9;
}
.info{
    width: 70.8rem;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
}
.title{
    padding-top: 1.3rem;
    font-family: 'Poppins', sans-serif;
    font-weight: 400;
    font-size: 1.8rem;
    line-height: 2.7rem;
    text-transform: uppercase;
}
.date{
    padding-top: 1.9rem;
    font-family: 'Poppins', sans-serif;
    font-weight: 400;
    font-size: .8rem;
    line-height: 1.2rem;
    text-transform: uppercase;
    color: #3C3C3C;
}
.infoInner{
    margin: 1.3rem 0 2.3rem;
    width: 100%;
    display: grid;
    grid-template-columns: 15.6rem 23rem 1fr;

}
.point{
    margin-bottom: .7rem;
    height: 100%;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
}
.pointName{
    font-family: 'Nunito', sans-serif;
    font-weight: 300;
    font-size: 1.4rem;
    line-height: 1.9rem;
}
.pointContent{
    margin-left: 1rem;
    font-family: 'Poppins', sans-serif;
    font-weight: 400;
    font-size: 1.6rem;
    line-height: 2.4rem;
    text-transform: uppercase;
}
.rating{
    height: 3.4rem;
    display: flex;
    align-items: center;
}
.asterisks{

}
.asterisks svg{
    margin-right: .8rem;
    width: 3.4rem;
    height: 3.4rem;
}
.ratingNumber{
    margin-left: .9rem;
    font-family: 'Nunito', sans-serif;
    font-weight: 400;
    font-size: 2.2rem;
    line-height: 3rem;
    text-align: center;
}
.mark{
    font-family: 'Poppins', sans-serif;
    font-size: 2.8rem;
    line-height: 4.2rem;
    text-transform: uppercase;
}
@media (max-width: 768px) {
    .orders{
        min-height: 100vh;
    }
    .header{
        margin: 3.8rem auto 3rem 3rem;
        font-size: 2.2rem;
    }
    .order{
        margin-left: 0;
        padding: 0 0 4.6rem 0;
        width: 100%;
        flex-direction: column;
    }
    .order:last-child{
        padding-bottom: 0;
    }
    .itemImage{
        margin-right: 0;
        width: 100%;
        min-height: 37rem;
        height: auto;
        object-fit:cover;
        object-position: center bottom;
    }
    .info{
        padding: 0 1rem;
        width: 100%;
    }
    .title{
        width: 80%;
    }
    .infoInner{
        grid-template-columns: 1fr;

    }
    .point{
        width: 100%;
    }
    .rating{
        width: 100%;
        justify-content: center;
    }

    .mark{
        width: 100%;
        text-align: center;
        margin-top: 2rem;
        line-height: normal;
    }
}